import {makeAutoObservable, toJS} from "mobx"
import {BorrowSupplyItem} from "models/types"
import {t} from "translations/translate"
import {Logger} from "utils/logger"
import Big from "big.js"
import {formatToNumber} from "utils/utils"
import {REG} from "models/constants/constants"
import {BigNumber, ethers} from "ethers"

export class ValuationViewModel {
  item: BorrowSupplyItem = {} as any
  isRefreshing = true
  isDeposit = true
  borrowLimit = 0
  totalBorrow = 0
  numberFilter = REG.NUMBER_INPUT
  inputValue = ""

  constructor() {
    makeAutoObservable(this, undefined, { autoBind: true });
  }

  get isEnoughBalance() {
    return Big(this.item.balance).gte(+this.inputValue);
  }

  get isMobile() {
    return window.innerWidth < 740;
  }

  get getInputFontSize() {
    return this.isMobile
      ? this.inputValue.length < 8
        ? "35px"
        : "22px"
      : this.inputValue.length < 8
        ? "50px"
        : "32px";
  }

  get getTokenSymbol() {
    return this.item.symbol
  }

  get getTitle() {
    return `${t(this.isDeposit ? "main.deposit" : "main.borrow")} ${this.item.symbol}`
  }

  get getTokenBalance() {
    return `${Big(this.item.balance).toFixed(2)}`
  }

  get getTokenUsdValue() {
    return `$${this.item.tokenUsdValue.toFixed(2)}`
  }

  get getDepositPerYear() {
    return `${this.item.supplyApy}%`
  }

  get getBorrowLimit() {
    return `${this.borrowLimit.toFixed(2)}$`;
  }

  get getBorrowLimitUsed() {
    return `${formatToNumber(this.borrowLimitUsed())}%`
  }

  borrowLimitUsed = () => {
    if (this.borrowLimit) {
      const limit = (this.totalBorrow / this.borrowLimit) * 100;
      return parseFloat(limit.toFixed(2));
    }

    return 0;
  }

  get isEther() {
    return this.item.symbol === "ETH";
  }

  get getInputValue() {
    return this.inputValue.replace(/^\D+\.?\D*$/,'')
  }

  setInputValue = (value: string) => {
    this.inputValue = value
  }

  mounted = async (isDeposit: boolean, item: string, borrowLimit: number, totalBorrow: number) => {
    try {
      this.item = JSON.parse(item) as any

      console.log(this.item.underlyingDecimals)
    } catch (e) {
      Logger.info("ERROR", e)
    }
    this.borrowLimit = borrowLimit
    this.totalBorrow = totalBorrow

    this.setLoader(false)
  }

  unmounted = () => {}

  setLoader = (state: boolean) => {
    this.isRefreshing = state
  }

  setMaxValue = (value: string) => {
    const isInvalidValue = !new RegExp(REG.NUMBER).test(value);

    this.inputValue = isInvalidValue
      ? String(value).substring(0, 16)
      : value || "0";
  }

  get newBorrowLimit() {
    if (!this.inputValue) return 0;
    if (!this.item.isEnteredTheMarket) return this.borrowLimit;

    return this.borrowLimit + this.inputValueUsd * this.collateralMantissa;
  }

  get collateralMantissa() {
    // const decimalValue = Big(10).pow(+this.item.underlyingDecimals)
    //
    // const ibi = Big(this.item.totalSupply).div(decimalValue)
    // console.log("ibbi", ibi)

    console.log("mdaa", +this.item.underlyingDecimals)

    // @ts-ignore
    return this.item.collateralFactorMantissa / 1e18;
  }

  get inputValueUsd() {
    // @ts-ignore
    return parseInt(this.inputValue) * this.item.tokenUsdValue;
  }
}